<h2 id="what-is-the-java-decompiler" tabindex="-1"><a class="header-anchor" href="#what-is-the-java-decompiler" aria-hidden="true">#</a> What is the Java Decompiler?</h2>
<p>编译&amp;反编译
前端编译 javac -&gt; .class字节码文件
后端编译 HotSpot中的JIT编译器 -&gt; 机器语言</p>
<p>反编译的好处</p>
<ol>
<li>Java中的语法糖，泛型，自动拆箱，自动装箱</li>
</ol>
<p>javac -&gt; 解糖 -&gt; 反编译 -&gt; 语法糖底层的实现</p>
<p>反编译命令：javap</p>
<p>利用javax.tools动态编译执行java代码</p>
